export const getters = {
	// 得到是否登录
  GET_ISLOGIN: (state) => state.isLogin,
  // 获取是否在加载
  GET_ISLOADING: (state) => state.isLoading,
  GET_SCODE: (state) => state.sCode,
  GET_DEPARTMENTID: (state) => state.departmentId,
  GET_MAJORID: (state) => state.majorId,
}


